import React, { useState, useEffect } from 'react'
import Papa from 'papaparse'
import ReactSlider from 'react-slider'
import { CCard, CCardBody, CCol, CCardHeader, CRow, CContainer } from '@coreui/react'
import { CChartBar, CChartLine } from '@coreui/react-chartjs'
import { DocsCallout } from 'src/components'
import './MySlider.css'

const Dashboard = () => {
  const [exportsData, setExportsData] = useState([])
  const [importsData, setImportsData] = useState([])
  const [selectedCountries, setSelectedCountries] = useState([])
  const [loading, setLoading] = useState(true)
  useEffect(() => {
    //const importsFilePath = '/imports.csv'
    const importsFilePath =
      'https://raw.githubusercontent.com/tdushime/Imp_Exp_Dashboard/master/public/imports.csv'
    const labels =
      exportsData && exportsData[0]
        ? Object.keys(exportsData[0])
            .filter((key) => /^\d{4}$/.test(key))
            .map((year) => parseInt(year))
        : []
    Papa.parse(importsFilePath, {
      download: true,
      header: true,
      dynamicTyping: true,
      complete: (result) => {
        console.log('imports results: ', result.data)
        setImportsData(result.data)
        setLoading(false)
      },
    })
    //const exportsFilePath = '/exports.csv'
    const exportsFilePath =
      'https://raw.githubusercontent.com/tdushime/Imp_Exp_Dashboard/master/public/exports.csv'
    Papa.parse(exportsFilePath, {
      download: true,
      header: true,
      dynamicTyping: true,
      complete: (result) => {
        setExportsData(result.data)
        setLoading(false)
      },
    })
  }, [])
  const labels =
    exportsData && exportsData[0]
      ? Object.keys(exportsData[0])
          .filter((key) => /^\d{4}$/.test(key))
          .map((year) => parseInt(year))
      : []
  console.log('exportsdata: ', exportsData)
  console.log('importsdata: ', importsData)
  const countries = [...new Set(exportsData.map((item) => item.country))].filter(Boolean)
  const [selectedYear, setSelectedYear] = useState('')
  const [selectedYearRange, setSelectedYearRange] = useState([2015, 2018])
  const handleYearChange = (newYearRange) => {
    setSelectedYearRange(newYearRange)
  }
  const getYearsInRange = () => {
    const [start, end] = selectedYearRange
    return labels.filter((year) => year >= start && year <= end)
  }
  const customColorPalette = [
    '#0B5394',
    '#741B47',
    '#351C75',
    '#38761D',
    '#B45F06',
    '#990000',
    '#BF900F',
  ]
  const [activeCountries, setActiveCountries] = useState(countries)
  const [activeLines, setActiveLines] = useState([countries.map((country) => country)]) // Initialize all lines as active
  const exportsDatasets = countries
    .filter((country) => country !== null)
    .map((country, index) => {
      const countryData = exportsData.filter((item) => item.country === country)
      const countryDataArray = labels.map((year) => countryData[0][year])
      return {
        label: country,
        data: countryDataArray,
        backgroundColor: customColorPalette[index % customColorPalette.length],
        borderColor: customColorPalette[index % customColorPalette.length],
        pointBorderColor: customColorPalette[index % customColorPalette.length],
        fill: false,
      }
    })
  const importsDatasets = countries
    .filter((country) => country !== null)
    .map((country, index) => {
      const countryData = importsData.filter((item) => item.country === country)
      const countryDataArray = labels.map((year) => countryData[0][year])
      return {
        label: country,
        data: countryDataArray,
        backgroundColor: customColorPalette[index % customColorPalette.length],
        borderColor: customColorPalette[index % customColorPalette.length],
        pointBorderColor: customColorPalette[index % customColorPalette.length],
        fill: false,
      }
    })
  const options = {
    responsive: true,
    maintainAspectRatio: true,
    plugins: {
      legend: {
        position: 'right',
        display: true,
        align: 'start',
        labels: {
          boxWidth: window.innerWidth <= 768 ? 10 : 20,
          fontSize: window.innerWidth <= 768 ? 10 : 14,
        },
      },
    },
    scales: {
      x: {
        grid: {
          drawOnChartArea: false,
        },
      },
      y: {
        ticks: {
          beginAtZero: true,
          callback: function (value) {
            return formatYAxisTick(value)
          },
          // maxTicksLimit: 50,
          // stepSize: 100000000000,
          // max: 600000000000,
        },
      },
    },
    elements: {
      line: {
        tension: 0.0,
      },
      point: {
        radius: 2,
        hitRadius: 10,
        hoverRadius: 4,
        hoverBorderWidth: 3,
      },
    },
  }
  const filteredLabels = getYearsInRange()
  const importsChartData = {
    labels: filteredLabels,
    datasets: importsDatasets.map((dataset) => ({
      ...dataset,
      data: dataset.data.filter((value, index) => filteredLabels.includes(labels[index])),
    })),
  }
  const exportsChartData = {
    labels: filteredLabels,
    datasets: exportsDatasets.map((dataset) => ({
      ...dataset,
      data: dataset.data.filter((value, index) => filteredLabels.includes(labels[index])),
    })),
  }
  console.log('labels: ', labels)
  console.log('YearRange: ', labels[0], labels[labels.length - 1])
  console.log('countries: ', countries)
  console.log('Imports chartData: ', importsChartData)
  console.log('Exports chartData: ', exportsChartData)
  function formatYAxisTick(value) {
    if (value >= 1000000000) {
      return (value / 1000000000).toFixed(1) + 'B'
    }
    return value
  }
  const combinedChartData = {
    labels: filteredLabels,
    datasets: [
      ...exportsDatasets.map((dataset) => ({
        ...dataset,
        label: `Exports - ${dataset.label}`,
        data: dataset.data.filter((value, index) => filteredLabels.includes(labels[index])),
      })),
      ...importsDatasets.map((dataset) => ({
        ...dataset,
        label: `Imports - ${dataset.label}`,
        data: dataset.data.filter((value, index) => filteredLabels.includes(labels[index])),
      })),
    ],
  }

  return (
    <CContainer>
      <CRow>
        <CCol>
          <h4 className="text-center">Select Year Range</h4>
          <ReactSlider
            className="custom-slider"
            thumbClassName="custom-slider-thumb"
            trackClassName="custom-slider-track"
            renderThumb={(props, state) => <div {...props}>{state.valueNow}</div>}
            markClassName="custom-slider-mark"
            value={selectedYearRange}
            onChange={handleYearChange}
            min={labels[0]}
            max={labels[labels.length - 1]}
            marks={labels.length}
          />
        </CCol>
      </CRow>
      <CRow>
        <CCol xs={12} md={12} lg={6}>
          <CCard className="mb-4">
            <CCardHeader>Imports</CCardHeader>
            <CCardBody>
              <CChartBar data={importsChartData} options={options} />
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs={12} md={12} lg={6}>
          <CCard className="mb-4">
            <CCardHeader>Imports</CCardHeader>
            <CCardBody>
              <CChartLine data={importsChartData} options={options} />
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs={12} md={12} lg={6}>
          <CCard className="mb-4">
            <CCardHeader>Exports</CCardHeader>
            <CCardBody>
              <CChartBar data={exportsChartData} options={options} />
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs={12} md={12} lg={6}>
          <CCard className="mb-4">
            <CCardHeader>Exports</CCardHeader>
            <CCardBody>
              <CChartLine data={exportsChartData} options={options} />
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs={12} md={12} lg={12}>
          <CCard className="mb-4">
            <CCardHeader>Combined Imports and Exports Chart</CCardHeader>
            <CCardBody>
              <CChartLine data={combinedChartData} options={options} />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </CContainer>
  )
}

export default Dashboard
